import ContactForm from '../ContactForm/ContactForm'
import React, { useState } from 'react';

export function Contact() {
  const [showForm, change] = useState(true);

  function toggle(){
    change(!showForm)
  }

  return (
    <div className="Contact">
      <p onClick={toggle}>
        Contact section.
      </p>
      {showForm ? (<ContactForm/>):(<p>Nothign to see here</p>)}
      
    </div>
  );
}

