import logo from './logo.svg';
import './App.css';

import {BrowserRouter as Router, Switch, Link, useLocation, Route} from 'react-router-dom';
import Homepage from './components/Homepage/Homepage.js'
import Studies from './components/Studies/Studies.js'
import Projects from './components/Projects/Projects.js'
import {Contact} from './components/Contact/Contact.js'
import Navbar from './components/Navbar/Navbar.js'
import Clock from './components/Clock/Clock.js'


const mainPages = [
  "Homepage",
  "Studies",
  "Projects",
  "Contact"
]

function FountPage(props){
  let address = useLocation();
  return <div>
          <h2>Page not foung</h2>
          <p>The page: { address.pathname } was not found</p>
        </div>;

}

function App() {
  return (
    <div className="App">
      <Router>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <Navbar pages={mainPages}/>
          {/* <Link to='/'>Homepage</Link>
          <Link to='/studies'>Studies</Link>
          <Link to='/projects'>Projects</Link>
          <Link to='/contact'>Contact</Link> */}
        </header>

        <Switch>
          <Route path="/homepage"> 
            <Homepage/>
          </Route>
          <Route path="/studies">
            <Studies/>
          </Route>
          <Route path="/projects">
            <Projects/>
          </Route>
          <Route path="/contact">
            <Contact></Contact>
          </Route>
          {/* Important to add the root almost at the end, just before the default, and add the exact */}
          <Route exact path="/"> 
            <Homepage/>
          </Route>
          {/* This option need to be the last one */}
          <Route path="*"> 
            <FountPage/>
          </Route>
        </Switch>

        <footer className="App-footer">
          <p>
            Copyright?
          </p>
          <Clock/>
        </footer>

      </Router>
    </div>
  );
}

export default App;
