import React from 'react';
import './ContactForm.css';

class ContactForm extends React.Component {
    constructor(props) {
      super(props);
      this.state = {name: '', message: ''};
  
      this.executeNameChange = this.executeNameChange.bind(this);
      this.executeMessageChange = this.executeMessageChange.bind(this);
      this.executeSubmit = this.executeSubmit.bind(this);
    }
  
    executeNameChange(event) {
      let temp = event.target.value[event.target.value.length-1];
      if(temp !== ("!") && temp !== ("-") && temp !== ("/") && temp !== ('\\') && temp !== ('(') && temp !== (')') && temp !== ('\'') && temp !== ('`'))
        this.setState({name: event.target.value});
    }

    executeMessageChange(event) {
      let temp = event.target.value[event.target.value.length-1];
      if(temp !== ("!") && temp !== ("-") && temp !== ("/") && temp !== ('\\') && temp !== ('(') && temp !== (')') && temp !== ('\'') && temp !== ('`'))
        this.setState({message: event.target.value});
    }

    executeSubmit(event) {
      if(this.state.name !== '' && this.state.message!== '')
        alert('Hi '+ this.state.name +' your message "'+ this.state.message +'" was sent (not really)')
      event.preventDefault();
    }
  
    render() {
      return (
        <form onSubmit={this.executeSubmit}>
          <label>Nombre
            <input type="text" value={this.state.name} onChange={this.executeNameChange} placeholder="John Doe" />
          </label><br/>
          <label>Message          
            <textarea value={this.state.message} onChange={this.executeMessageChange} placeholder="Hello world!"/>
          </label><br/>
            <button type="submit">Sent message</button>
        </form>
      );
    }
  }

  export default ContactForm;