function Navbar(props) {
  return (
    <div className="Navbar">
      <ul>
      {props.pages.map((element)=>{
        return <li key={element}><Link to={element.toLowerCase()}>{element}</Link></li>
      })}
      </ul>
    </div>
  );
}

export default Navbar;
