import React from 'react';

/**
 * Usign the clock component developed by the teacher cause I think it is pretty cool
 */


class Clock extends React.Component {
  constructor(props) {
    super(props); // Invoca al constructor base
    this.state = {date: new Date(),
    showPhrase: true};
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.iteración(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  iteración() {
    this.setState({
      date: new Date()
    });
  }
  
  toggle(){
    this.setState({
      showPhrase: !this.state.showPhrase
    });
  }

  render() {
    const showPhrase = this.state.showPhrase;
    return (
      <div>
        {showPhrase ? (
          <p onClick={this.toggle.bind(this)}>Local time: {this.state.date.toLocaleTimeString()}</p>
        ):(
          <p onClick={this.toggle.bind(this)}> {this.state.date.toLocaleTimeString()}</p>
        )}
        
      </div>
    );
  }
}
    
export default Clock;